export default {
	data() {
		let that = this;
		return {
			model: {
				name: '',
				phone:'',
				deliveryUnit:'',
				code:''
			},
			rules:{
				name: [
					{ 
						required: true, 
						message: '请输入姓名', 
						trigger: 'blur' ,
					}, 
					{ 
						min: 3, 
						max: 5, 
						message: '姓名长度在3到5个字符', 
						trigger: ['change','blur'],
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.chinese(value);
						},
						message: '姓名必须为中文',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change','blur'],
					},
					
				],
				code: [
					{ 
						required: true, 
						message: '请输入取件码', 
						trigger: 'blur' ,
					}, 
					{ 
						min: 4, 
						max: 6, 
						message: '取件码在4到6个数字', 
						trigger: ['change','blur'],
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.number(value);
						},
						message: '取件码必须为数字',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change','blur']
					},
					
				],
				phone: [
					{
						required: true, 
						message: '请输入手机号',
						trigger: ['change','blur'],
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.mobile(value);
						},
						message: '手机号码不正确',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change','blur'],
					}
				]
			},
			deliveryUnitList: [
				{
					value: '中通',
					label: '中通'
				},
				{
					value: '申通',
					label: '申通'
				},
				{
					value: '圆通',
					label: '圆通'
				},
				{
					value: '百世',
					label: '百世'
				}
			],
			
			// 底部选项栏
			selectShow:false,
			border: true,
			labelPosition: 'left',
			check: false,
			selectStatus: 'close',

			radioCheckWidth: 'auto',
			radioCheckWrap: false,
			
			codeTips: '',
			errorType: ['message'],
		};
	},
	onLoad() {
		
	},
	computed: {
		borderCurrent() {
			return this.border ? 0 : 1;
		}
	},
	onReady() {
		this.$refs.uForm.setRules(this.rules);
	},
	methods: {
		// 下单  提交表单
		submit(){
			console.log("提交信息")
		},
		// 底部状态栏
		selectConfirm(e) {
			this.model.deliveryUnit = '';
			e.map((val, index) => {
				this.model.deliveryUnit += this.model.deliveryUnit == '' ? val.label : '-' + val.label;
			})
		}
	}
};
